<template>
  <div class="box-wrapper" :style="{ transform: 'rotateX('+ upDown +'deg) rotateY('+ leftRight +'deg)' }">
    <div class="box box1">
      <Movie v-for="(item, index) in movieData.list1" :key="index" :movie="item" @to-movie="toMovie"/>
    </div>
    <div class="box box2">
      <Movie v-for="(item, index) in movieData.list2" :key="index"/>
    </div>
    <div class="box box3">
      <Movie v-for="(item, index) in movieData.list3" :key="index"/>
    </div>
    <div class="box box4">
      <Movie v-for="(item, index) in movieData.list4" :key="index"/>
    </div>
    <div class="box box5">
      <slot name="publish"></slot>
    </div>
    <div class="box box6">
      <slot name="detail"></slot>
    </div>
  </div>
</template>

<script>
import Movie from './Movie.vue'

import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  name: 'Box',
  components: {
    Movie
  },
  props: {
    leftRight: {
      type: Number,
      default: 0
    },
    upDown: {
      type: Number,
      default: 0
    }
  },
  computed: mapState(['movieData']),
  methods: {
    ...mapActions(['getMovie']),
    ...mapMutations(['setThisMovie']),
    toMovie (data) {
      this.setThisMovie(data)
      this.$emit('to-movie')
    }
  },
  created () {
    this.getMovie()
  }
}
</script>

<style lang="less" scoped>
.box-wrapper{
  position: relative;
  width: 400px;
  height: 400px;
  transform-style: preserve-3d;
  transform-origin: 200px 200px 200px;
  user-select: none;
  transition: all 0.3s;
  .box{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border: 1px solid #ccc;
    background-color: rgba(255, 255, 255, 0.5);
    &.box1, &.box2, &.box3, &box4{
      display: flex;
      flex-wrap: wrap;
    }
    &.box1{
      transform: translateZ(400px);
    }
    &.box2{
      transform-origin: right;
      transform: rotateY(90deg);
    }
    &.box3{
      transform-origin: center;
      transform: rotateY(180deg);
    }
    &.box4{
      transform-origin: left;
      transform: rotateY(-90deg);
    }
    &.box5{
      transform-origin: top;
      transform: rotateX(90deg);
    }
    &.box6{
      transform-origin: bottom;
      transform: rotateX(-90deg);
    }
  }
}
</style>
