<template>
  <div id="app">
    <div id="toast"></div>
    <Header/>
    <Box :leftRight="leftRight" :upDown="upDown" @to-movie="downOpt">
      <template slot="publish">
        <Publish :leftRight="leftRight" @back="backOpt"/>
      </template>
      <template slot="detail">
        <Detail :leftRight="leftRight"/>
      </template>
    </Box>
    <Footer :leftRight="leftRight" :upDown="upDown" @pre-opt="preOpt" @next-opt="nextOpt" @back-opt="backOpt" @home-opt="homeOpt"/>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import Box from './components/Box.vue'
import Footer from './components/Footer.vue'
import Publish from './components/Publish.vue'
import Detail from './components/Detail.vue'

export default {
  name: 'app',
  components: {
    Header,
    Box,
    Footer,
    Publish,
    Detail
  },
  data () {
    return {
      leftRight: 0,
      upDown: 0
    }
  },
  methods: {
    preOpt () {
      this.upDown = 0
      this.leftRight += 90
    },
    nextOpt () {
      this.upDown = 0
      this.leftRight -= 90
    },
    backOpt (type) {
      if (type === '1') {
        this.upDown = -90 - this.upDown
      } else if(type === '2') {
        this.upDown = 0
      }
    },
    downOpt () {
      this.upDown = 90 - this.upDown
    },
    homeOpt () {
      this.leftRight = 0
      this.upDown = 0
    }
  }
}
</script>

