<template>
  <div class="publish-wrapper" :style="{ transform: 'rotateZ('+ leftRight +'deg)' }">
    <div class="movie-title">
      <input type="text" class="title" placeholder="电影名称（必填）" v-model="movie.title">
    </div>
    <div class="movie-info">
      <div class="poster" :style="{ backgroundImage: 'url(' + movie.poster + ')'}">
        <input type="file" accept="*.png,*.jpg,*.jpeg" class="poster-input" @change.stop="changePoster">
        <span class="plus" v-if="movie.poster === ''">+</span>
        <span v-if="movie.poster === ''">添加海报</span>
      </div>
      <div class="info">
        <input type="text" class="info-item" placeholder="导演（必填）" v-model="movie.director">
        <input type="text" class="info-item" placeholder="主演（必填）" v-model="movie.actor">
        <input type="text" class="info-item" placeholder="类型（必填）" v-model="movie.type">
        <input type="text" class="info-item" placeholder="制片国家/地区（必填）" v-model="movie.country">
        <input type="text" class="info-item" placeholder="片长（必填）" v-model="movie.duration">
        <input type="text" class="info-item" placeholder="上映日期（必填）" v-model="movie.showtime">
        <input type="text" class="info-item" placeholder="评分（必填）" v-model="movie.score">
      </div>
    </div>
    <textarea cols="30" rows="4" class="movie-desc" placeholder="简介（必填）" v-model="movie.desc"></textarea>
    <div class="movie-link" v-for="(item, i) in movie.download" :key="i">
      <input type="text" class="link" placeholder="资源网站或链接（必填）" v-model="item.link">
      <div class="btn" @click="delOpt(i)" v-if="i > 0">删除</div>
      <div class="btn" @click="addOpt">新添</div>
    </div>
    <div class="save-btn" @click="saveOpt">保存</div>
  </div>
</template>

<script>
import lrz from 'lrz'
import { mapActions } from 'vuex'

export default {
  name: 'Publish',
  props: {
    leftRight: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      movie: {
        title: '',
        poster: '',
        director: '',
        actor: '',
        type: '',
        country: '',
        duration: '',
        showtime: '',
        score: '',
        desc: '',
        download: [{
          link: ''
        }]
      }
    }
  },
  methods: {
    ...mapActions(['addMovie']),
    initData () {
      this.movie = {
        title: '',
        poster: '',
        director: '',
        actor: '',
        type: '',
        country: '',
        duration: '',
        showtime: '',
        score: '',
        desc: '',
        download: [{
          link: ''
        }]
      }
    },
    changePoster () {
      lrz(event.currentTarget.files[0]).then((rst) => {
        this.movie.poster = rst.base64
      }).catch(() => {
        this.$toast('图片上传失败')
      })
    },
    delOpt (index) {
      this.movie.download.splice(index, 1)
    },
    addOpt () {
      this.movie.download.push({
        link: ''
      })
    },
    async saveOpt () {
      if (this.movie.title.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影名称'
        })
        return
      }
      if (this.movie.poster === '') {
        this.$toast({
          title: '请添加海报'
        })
        return
      }
      if (this.movie.director.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影导演'
        })
        return
      }
      if (this.movie.actor.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影主演'
        })
        return
      }
      if (this.movie.type.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影类型'
        })
        return
      }
      if (this.movie.country.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影制片国家/地区'
        })
        return
      }
      if (this.movie.duration.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影片长'
        })
        return
      }
      if (this.movie.showtime.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影上映时间'
        })
        return
      }
      if (this.movie.score.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影评分'
        })
        return
      }
      if (this.movie.desc.replace(/\s/gi, '') === '') {
        this.$toast({
          title: '请填写电影简介'
        })
        return
      }
      for (let index = 0; index < this.movie.download.length; index++) {
        const element = this.movie.download[index]
        if (element.link.replace(/\s/gi, '') === '') {
          this.$toast({
            title: '请填写完整的资源网站或链接'
          })
          return
        }
      }
      let res = await this.addMovie(this.movie)
      if (res) {
        this.$toast({
          title: '发布成功'
        })
        this.initData()
        this.$emit('back')
      }
    }
  }
}
</script>

<style lang="less">
.publish-wrapper{
  height: 100%;
  overflow-y: auto;
  transform-origin: center center;
  padding: 20px;
  textarea, input[type="text"]{
    border: none;
    outline: none;
    transition: all 0.3s;
    border: 1px solid #ccc;
    &:focus{
      border-color: #333;
    }
  }
  .movie-title{
    margin-bottom: 10px;
    .title{
      width: 100%;
      padding: 4px 8px;
    }
  }
  .movie-info{
    display: flex;
    align-items: center;
    .poster{
      width: 140px;
      height: 193px;
      border: 1px solid #ccc;
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-size: 12px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-color: #fff;
      cursor: pointer;
      .poster-input{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        z-index: 10;
        cursor: pointer;
      }
      .plus{
        line-height: 25px;
        font-size: 40px;
      }
    }
    .info{
      flex: 1;
      margin-left: 10px;
      .info-item{
        display: block;
        width: 100%;
        font-size: 10px;
        padding: 3px 6px;
        margin-top: 10px;
        &:first-child{
          margin-top: 0;
        }
      }
    }
  }
  .movie-desc{
    display: block;
    width: 100%;
    margin-top: 10px;
    resize: none;
    padding: 4px 8px;
    font-size: 10px;
  }
  .movie-link{
    margin-top: 10px;
    display: flex;
    .link{
      flex: 1;
      font-size: 10px;
      padding: 4px 8px;
    }
    .btn{
      display: flex;
      align-items: center;
      font-size: 10px;
      background-color: #333;
      color: #fff;
      padding: 0 5px;
      margin-left: 5px;
      cursor: pointer;
      border-radius: 3px;
    }
  }
  .save-btn{
    margin-top: 10px;
    font-size: 10px;
    padding: 5px 0;
    text-align: center;
    background-color: #333;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
  }
}
</style>
