<template>
  <div class="movie-wrapper" :title="movie.title" @click="toMovie(movie)">
    <div class="poster" :style="{ backgroundImage: 'url(' + movie.poster + ')'}"></div>
    <div class="title">{{movie.title}}</div>
    <div class="time">更新日期：{{formatTime(movie.createdAt)}}</div>
  </div>
</template>

<script>
import { formatTime } from '../plugins/func'

export default {
  name: 'Movie',
  props: {
    movie: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  methods: {
    formatTime,
    toMovie (data) {
      this.$emit('to-movie', data)
    }
  }
}
</script>

<style lang="less" scoped>
.movie-wrapper{
  width: 50%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  &:hover{
    .title{
      color: #000;
    }
  }
  .poster{
    width: 100px;
    height: 130px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .title{
    margin-top: 5px;
    font-size: 10px;
    transition: all 0.3s;
  }
  .time{
    font-size: 10px;
    color: #999;
    transform: scale(0.8, 0.8);
  }
}
</style>
