<template>
  <div class="footer-wrapper">
    <div class="btn big" @click="preOpt">上一页</div>
    <div class="btn" @click="backOpt('2')" v-if="upDown === 90">返回</div>
    <div class="btn" @click="backOpt('1')" v-else>{{upDown === -90 ? '返回' : '发布'}}</div>
    <div class="btn" @click="homeOpt">首页</div>
    <div class="btn big" @click="nextOpt">下一页</div>
  </div>
</template>

<script>
export default {
  name: 'Footer',
  props: {
    leftRight: {
      type: Number,
      default: 0
    },
    upDown: {
      type: Number,
      default: 0
    }
  },
  methods: {
    preOpt () {
      if (this.leftRight === 0) {
        return
      }
      this.$emit('pre-opt')
    },
    nextOpt () {
      this.$emit('next-opt')
    },
    backOpt (type) {
      this.$emit('back-opt', type)
    },
    homeOpt () {
      this.$emit('home-opt')
    }
  }
}
</script>

<style lang="less" scoped>
.footer-wrapper{
  margin-top: 200px;
  text-align: center;
  font-size: 12px;
  .btn{
    display: inline-block;
    margin-left: 30px;
    padding: 4px 12px;
    background-color: #333;
    color: #fff;
    border-radius: 15px;
    cursor: pointer;
    &:first-child{
      margin-left: 0;
    }
    &.big{
      padding: 6px 18px;
    }
  }
}
</style>
