import Vue from 'vue'
import Vuex from 'vuex'

import { API } from './api/index'
import Request from './api/request'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    movieData: {
      all: {},
      list1: [],
      list2: [],
      list3: [],
      list4: [] 
    },
    pageData: {
      page: '1',
      total: 0
    },
    thisMovie: {}
  },
  mutations: {
    setMovieData (state, data) {
      state.movieData.all[state.pageData.page] = data.results
      state.pageData.total = data.count
      if (data.results.length > 12) {
        state.movieData.list4 = data.results.slice(12, data.results.length)
        state.movieData.list3 = data.results.slice(8, 12)
        state.movieData.list2 = data.results.slice(4, 8)
        state.movieData.list1 = data.results.slice(0, 4)
      } else if (data.results.length > 8 && data.results.length <= 12) {
        state.movieData.list4 = []
        state.movieData.list3 = data.results.slice(8, data.results.length)
        state.movieData.list2 = data.results.slice(4, 8)
        state.movieData.list1 = data.results.slice(0, 4)
      } else if (data.results.length > 4 && data.results.length <= 8) {
        state.movieData.list4 = []
        state.movieData.list3 = []
        state.movieData.list2 = data.results.slice(4, data.results.length)
        state.movieData.list1 = data.results.slice(0, 4)
      } else if (data.results.length > 0 && data.results.length <= 4) {
        state.movieData.list4 = []
        state.movieData.list3 = []
        state.movieData.list2 = []
        state.movieData.list1 = data.results.slice(0, data.results.length)
      } else {
        state.movieData.list4 = []
        state.movieData.list3 = []
        state.movieData.list2 = []
        state.movieData.list1 = []
      }
    },
    setThisMovie (state, data) {
      state.thisMovie = data
    }
  },
  actions: {
    async getMovie ({ commit }) {
      try {
        let config = {
          params: {
            order: '-createdAt',
            limit: 16,
            skip: 0,
            count: 1,
          }
        }
        let res = await Request.get(API.movieAPI, config)
        if (res && res.results) {
          commit('setMovieData', res)
        }
        return true
      } catch (error) {
        return false
      }
    },
    async addMovie ({ dispatch }, options) {
      try {
        let res = await Request.post(API.movieAPI, options)
        if (res) {
          dispatch('getMovie')
        }
        return true
      } catch (error) {
        return false
      }
    }
  }
})
